<template>
    <Tabs class="options-default" :animated="false">
        <TabPane label="样式">
            <!-- 基本样式 -->
            <div class="item item-5">
                <span class="name w3">高度:</span>
                <InputNumber
                    :step="10"
                    v-model="wrapStyle.height"
                    size="small"
                    :min="0"
                ></InputNumber>
            </div>
            <div class="item item-5">
                <span class="name w3">宽度:</span>
                <InputNumber
                    :step="10"
                    v-model="wrapStyle.width"
                    size="small"
                    :min="0"
                ></InputNumber>
            </div>
            <div class="item item-5">
                <span class="name w3">X轴:</span>
                <InputNumber
                    :step="10"
                    v-model="wrapStyle.left"
                    size="small"
                ></InputNumber>
            </div>
            <div class="item item-5">
                <span class="name w3">Y轴:</span>
                <InputNumber
                    :step="10"
                    v-model="wrapStyle.top"
                    size="small"
                ></InputNumber>
            </div>
            <span class="line"></span>
            <slot></slot>
            <!-- 背景相关 -->
            <div class="item">
                <span class="name">背景颜色:</span>
                <ColorPicker
                    v-model="wrapStyle['background-color']"
                    :hue="true"
                    alpha
                    @on-change="
                        colorChange('wrapStyle', {
                            'background-color': wrapStyle['background-color']
                        })
                    "
                />
                <br />
            </div>
            <div class="item">
                <span class="name">旋转角度:</span>
                <Slider
                    v-model="wrapStyle.transform"
                    :step="1"
                    :max="360"
                    :min="0"
                ></Slider>
            </div>
            <div class="item">
                <span class="name w6">圆角:</span>
                <Slider
                    v-model="nodeStyle['border-radius']"
                    :step="1"
                    :max="100"
                    :min="0"
                ></Slider>
            </div>
            <div class="item">
                <span class="name w6">透明度:</span>
                <Slider
                    v-model="wrapStyle.opacity"
                    :step="0.01"
                    :max="1"
                    :min="0"
                ></Slider>
            </div>
            <span class="line"></span>

            <!-- 高级属性 -->
            <div class="item">
                <span class="name">隐藏元素：</span>
                <i-switch
                    true-color="#57a3f3"
                    false-color="#d2d2d2"
                    @on-change="visibilityChange"
                    :value="wrapStyle.visibility !== 'hidden' ? false : true"
                />
            </div>
            <div class="group">
                <div class="item">
                    <span class="name">开启阴影：</span>
                    <i-switch
                        true-color="#57a3f3"
                        false-color="#d2d2d2"
                        @on-change="showShadow = !showShadow"
                    />
                </div>
                <!-- <div class="block" v-if="showShadow">
                    <div class="item">
                        <span class="name">阴影设置:</span>
                        <Slider v-model="radius" :step="1" :max="100" :min="0"></Slider>
                    </div>
                    <div class="item">
                        <span class="name">阴影颜色:</span>
                        <ColorPicker v-model="bgColor" :hue="true" alpha />
                    </div>
                    <span class="line"></span>
                </div>-->
            </div>
            <div class="group">
                <div class="item">
                    <span class="name">开启边框：</span>
                    <i-switch
                        true-color="#57a3f3"
                        false-color="#d2d2d2"
                        :value="showBorder"
                        @on-change="borderShow"
                    />
                </div>
                <div class="block" v-if="showBorder">
                    <div class="item">
                        <span class="name">边框类型:</span>
                        <Select
                            v-model="nodeStyle['border-style']"
                            style="width:200px"
                        >
                            <Option
                                v-for="item in borderList"
                                :value="item.value"
                                :key="item.value"
                                >{{ item.label }}</Option
                            >
                        </Select>
                    </div>
                    <div class="item">
                        <span class="name">边框颜色:</span>
                        <ColorPicker
                            v-model="nodeStyle['border-color']"
                            :hue="true"
                            alpha
                            @on-change="
                                colorChange('wrapStyle', {
                                    'border-color': nodeStyle['border-color']
                                })
                            "
                        />
                    </div>
                    <div class="item">
                        <span class="name">边框大小:</span>
                        <InputNumber
                            :step="1"
                            :value="1"
                            size="small"
                            :min="0"
                            v-model="nodeStyle['border-width']"
                        ></InputNumber>
                    </div>
                    <span class="line"></span>
                </div>
            </div>
        </TabPane>
        <TabPane label="动画"></TabPane>
    </Tabs>
</template>

<script>
export default {
    name: "options-default",
    props: ["wrapStyle", "nodeStyle"],
    computed: {
        // 是否显示边框
        showBorder() {
            return this.nodeStyle["border-width"] == 0 ? false : true;
        }
    },

    methods: {
        // 调用父组件改变style的方法
        changeStyleValue(target, obj) {
            this.$emit("changePropsStyleValue", target, obj);
        },
        // 颜色改变
        colorChange(tagert, obj) {
            this.changeStyleValue(tagert, obj);
        },
        // 是否显示边框
        borderShow(status) {
            if (status) {
                this.changeStyleValue("nodeStyle", {
                    "border-width": 1
                });
            } else {
                this.changeStyleValue("nodeStyle", {
                    "border-width": 0
                });
            }
        },

        visibilityChange(status) {
            if (status) {
                this.changeStyleValue("wrapStyle", {
                    visibility: "hidden"
                });
            } else {
                this.changeStyleValue("wrapStyle", {
                    visibility: "inherit"
                });
            }
        }
    },
    data() {
        return {
            showShadow: false,
            borderList: [
                {
                    value: "solid",
                    label: "实线条"
                },
                {
                    value: "double",
                    label: "双线"
                },
                {
                    value: "dashed",
                    label: "虚线"
                },
                {
                    value: "dotted",
                    label: "点线"
                }
            ]
        };
    }
};
</script>

<style lang="less" scoped>
@import url("./style.less");
</style>
